import React, { Fragment } from 'react';
import { useState ,useEffect} from 'react';
import { FormGroup, Label, Input,Form, Container, Button } from 'reactstrap';
import axios from 'axios';
import base_url from "./../api/bootapi";
import { toast } from 'react-toastify';

const AddCourse =() =>
{
  useEffect(() => {
    document.title="Add Course";
})
const [course,setCourse]=useState([]);
  const handleForm=(e)=>{
  console.log(course);
  postDatatoServer(course);
  e.preventDefault();
  }

  //function to post data to server
  const postDatatoServer=(data)=>{
    axios.post(`${base_url}/Courses`).then(
      (response)=>{
       console.log(response);
       toast.success("Course has been added");
       setCourse({id: "", title: "", description: ""});
    },
    (error)=>
    {
        console.log(error);
        toast.error("Server Down",{position:"bottom-center"});
        setCourse({id: "", title: "", description: ""});
    }
    );
  };
    return <Fragment>
        <h1 className="text-center">Fill Course Detail</h1>
        <Form onSubmit={handleForm}>
            <FormGroup>
          <label for="courseId">Course Id </label>
              <Input type="text" placeholder="Enter Course Id" name="courseid" 
              id="courseId" 
              required
              onChange={(e)=>{
                setCourse({...course,id:e.target.value});
              }}/>
         
            </FormGroup>
            <FormGroup>
          <label for="title">Title</label>
              <Input type="text" placeholder="Enter title" required name="title" id="title" 
              onChange={(e)=>{
                setCourse({...course,title:e.target.value});
              }}/>
         
            </FormGroup>
            <FormGroup>
          <label for="description">Description </label>
              <Input type="textarea" placeholder="Enter description" required name="description" id="description" style={{height:100}}
              onChange={(e)=>{
                setCourse({...course,description:e.target.value});
              }}/>
         
            </FormGroup>
          <Container className="text-center">
            <Button type="submit" color="success">Add Course</Button>
            <Button type="reset" color="warning ml-2">Clear</Button>
          </Container>
        </Form>
    </Fragment>
}

export default AddCourse;